<template>
  <div class="character_box">
    <div id="character"></div>
    <div class="characters">
      <h1>角色介紹</h1>
      <div class="character_con">
        <div class="character" @click="chooseCharacter(0)">
          <img src="../assets/character.png" alt="" />
          <h2>圈圈妹</h2>
        </div>
      </div>
      <div class="character_con" style="justify-content:space-around">
        <div class="character" @click="chooseCharacter(1)">
          <img src="../assets/character4.png" alt="" />
          <h2>滴子</h2>
        </div>
        <div class="character" id="character_con2" @click="chooseCharacter(2)">
          <img src="../assets/character2.png" alt="" />
          <h2>皺哥</h2>
        </div>
      </div>
      <div class="character_con">
        <div class="character" @click="chooseCharacter(3)">
          <img src="../assets/character3.png" alt="" />
          <h2>小精靈</h2>
        </div>
      </div>
    </div>
    <div class="character_intro" v-if="!isCellPhone && showCharacterBox">
      <div class="character_cancle_cp" @click="closeCharacter">
        <i class="fas fa-times fa-2x"></i>
      </div>
      <div class="character_words_cp">
        <div class="character_pic_cp">
          <img :src="characterPic" alt="" />
        </div>
        <h1 class="character_words_cp">
          {{ character[activeCharacter].name }}
        </h1>
        <p>
          {{ character[activeCharacter].height }} <br />
          {{ character[activeCharacter].family }} <br />
          {{ character[activeCharacter].problem }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Character",
  data() {
    return {
      isCellPhone: false,
    };
  },
  computed: {
    character() {
      return this.$store.state.character;
    },
    activeCharacter() {
      return this.$store.state.activeCharacter;
    },
    characterPic(){
      return this.$store.state.picPath;
    },
    showCharacterBox() {
      if (this.$store.state.activeCharacter == null) {
        return false;
      } else return true;
    },
  },
  methods: {
    checkScreen() {
      let w = screen.width;
      if (w <= 768) {
        this.isCellPhone = true;
      }
    },
    showNavBox() {
      let box = document.getElementById("navBox");
      if (box.classList[1]) {
        box.classList.remove("showNavBox");
      } else {
        box.classList.add("showNavBox");
      }
    },
    chooseCharacter(num) {
      this.$store.dispatch("chooseCharacter", { num: num });
		},
		closeCharacter(){
      this.$store.dispatch('closeCharacter')
    }
  },
  mounted() {
    this.checkScreen();
  },
};
</script>

