  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 10px;
  align-items: center;
}

.character {
  width: 200px;
  height: 200px;
  cursor: pointer;
  position: relative;
}
.character h2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 2em;
  opacity: 0;
  user-select: none;
}

.character::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
  border-radius: 100%;
  background-color: #000;
  opacity: 0;
  transition: 0.3s;
}

.character:hover h2 {
  opacity: 1;
}

.character:hover::before {
  opacity: 0.5;
}
.character img {
  width: 100%;
}

.character_intro {
  position: absolute;
  width: 800px;
  background-color: #f2e4d2;
  height: 500px;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -60%);
  border-radius: 20px;
}

@media (max-width: 436px) {
  #character_con2 {
    margin-top: 10px;
  }
}

