import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: true,
    activeCharacter: null,
    picPath: "",
    character: {
      0: {
        name: "圈圈妹",
        height: `身高 : 120cm`,
        family: `家庭成員 : 八弟兩妹`,
        problem: `困擾  : 常常在學校被別人笑自己眼睛外面那麼一大圈黑眼圈，不管自己睡了一整個暑假，眼睛永遠不會改善。`,
      },
      1: {
        name: "滴子",
        height: `身高 : 50cm`,
        family: `家庭成員 : 獨生子`,
        problem: `困擾  : 夢想著當明星的他卻因為鼻子不夠挺而被拒絕，已嘗試過民俗療法每日一捏卻還是無法改善。`,
      },
      2: {
        name: "皺哥",
        height: `身高 : 13cm`,
        family: `家庭成員 : 一兄一妹`,
        problem: `困擾  : 吃素的他總是發現自己在睡夢中夾死了無數無辜的蚊子，造成每天晚上有無數的惡夢。`,
      },
      3: {
        name: "小精靈",
        height: `身高 : 40cm`,
        family: `因為一些意外變成調皮的小惡魔，闖入歐爺的工作室。在歐爺工作室搗亂，後被歐爺幫助下，變回原本的樣子。`,
        problem: ``,
      },
    },
  },
  mutations: {
    loadingIsDone(state) {
      state.loading = false;
    },
    chooseCharacter(state, num) {
      state.activeCharacter = num;
    },
    picPath(state, num) {
      let path = "";
      switch (num) {
        case 0:
          path = "../img/panda.png";
          break;
        case 1:
          path = "../img/fish.png";
          break;
        case 2:
          path = "../img/dog.png";
          break;
        case 3:
          path = "../img/monster.png";
          break;
      }
      state.picPath = path;
    },
    closeCharacter(state){
      state.activeCharacter = null;
    }
  },
  actions: {
    loadingIsDone(context) {
      context.commit("loadingIsDone");
    },
    chooseCharacter(context, payload) {
      context.commit("chooseCharacter", payload.num);
      context.commit("picPath", payload.num);
    },
    closeCharacter(context){
      context.commit("closeCharacter");
    }
  },
});
