<template>
  <div class="bg">
    <div class="d-inline-b">
      <div id="typewrite-animated">
        <span id="typewrite-text" class="d-inline-b"></span>
      </div>
			<div class="btn" @click="gotoMainPage">
				LOGINLIFE
			</div>
    </div>
    <div id="circle"></div>
  </div>
</template>

<script>
export default {
  name: "LoadingPage",
  computed: {
		loading () {
			return this.$store.state.loading
		}
	},
  methods: {
    Type() {
      const arr = ["你想改變你的人生嗎？"];
      // const arr = ["Do you want to change your life？"];
      const typewriteAni = document.getElementById("typewrite-animated");
      const typewriteText = document.getElementById("typewrite-text");
      typewriteAni.classList.add("typewriter");
      typewriteText.textContent = arr[0];
    },
    gotoMainPage() {
      document.getElementById('circle').classList.add("circle");
      setTimeout(()=>{
        this.$store.dispatch('loadingIsDone')
      },1300)
    }
  },
  mounted() {
		setTimeout(()=>{
			this.Type();
		},800)
  },
};
</script>

