import Vue from 'vue'
import App from './App.vue'
import store from './store'
import VueAsewsomeSwiper from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import VueScrollTo from 'vue-scrollto'

Vue.config.productionTip = false
Vue.use(VueAsewsomeSwiper);
Vue.use(VueScrollTo,{
  duration: 1000,
  force: true,
  cancelable: true,
  x: false,
  y: true
});

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
