<template>
  <div class="navbar">
    <div class="navbar_box">
      <div class="logo" v-scroll-to="'#home'"></div>
      <div class="navBtn" v-if="!isCellPhone">
        <ul>
          <li v-scroll-to="'#home'">Home</li>
          <li v-scroll-to="'#history'">History</li>
          <li v-scroll-to="'#films'">Stills x Film</li>
          <li v-scroll-to="'#character'">Character</li>
          <li v-scroll-to="'#staff'">Staff</li>
        </ul>
      </div>
      <div class="navBtn_cp" v-if="isCellPhone" @click="showNavBox()">
        <i class="fas fa-bars fa-2x"></i>
      </div>
      <div class="navBox" id="navBox" v-if="isCellPhone">
        <div class="navBox_close" @click="showNavBox()">
          <i class="fas fa-times fa-2x"></i>
        </div>
        <ul>
          <li @click="showNavBox()" v-scroll-to="'#home'"><h2>Home</h2></li>
          <li @click="showNavBox()" v-scroll-to="'#history'">
            <h2>History</h2>
          </li>
          <li @click="showNavBox()" v-scroll-to="'#films'">
            <h2>Stills x Film</h2>
          </li>
          <li @click="showNavBox()" v-scroll-to="'#character'">
            <h2>Character</h2>
          </li>
          <li @click="showNavBox()" v-scroll-to="'#staff'"><h2>Staff</h2></li>
        </ul>
        <div class="navBox_pic"></div>
      </div>
    </div>
    <div class="character_intro_cp" v-if="isCellPhone && showCharacterBox">
      <div class="character_cancle_cp" @click="closeCharacter"><i class="fas fa-times fa-2x"></i></div>
      <div class="character_words_cp">
        <div class="character_pic_cp">
          <img :src="characterPic" alt="">
        </div>
        <h1 class="character_words_cp">
          {{ character[activeCharacter].name }}
        </h1>
        <p>
          {{ character[activeCharacter].height }} <br />
          {{ character[activeCharacter].family }} <br />
          {{ character[activeCharacter].problem }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      isCellPhone: false,
    };
  },
  computed: {
    character() {
      return this.$store.state.character;
    },
    activeCharacter() {
      return this.$store.state.activeCharacter;
    },
    characterPic(){
      return this.$store.state.picPath;
    },
    showCharacterBox() {
      if (this.$store.state.activeCharacter == null) {
        return false;
      } else return true;
    },
  },
  methods: {
    checkScreen() {
      let w = screen.width;
      if (w <= 768) {
        this.isCellPhone = true;
      }
    },
    showNavBox() {
      let box = document.getElementById("navBox");
      if (box.classList[1]) {
        box.classList.remove("showNavBox");
      } else {
        box.classList.add("showNavBox");
      }
    },
    closeCharacter(){
      this.$store.dispatch('closeCharacter')
    }
  },
  mounted() {
    this.checkScreen();
  },
};
</script>
