  position: absolute;
  bottom: 20px;
  left: 40px;
  width: 50%;
  height: 200px;
  background-image: url("../assets/owl.png");
  background-size: contain;
  background-position: left;
  background-repeat: no-repeat;
}

.showNavBox {
  opacity: 1;
  visibility: visible;
  display: block;
}

.character_intro_cp {
  position: absolute;
